import {
    BlueSnapDirectSepaInstrument,
    OrderPaymentRequestBody,
    PaymentArgumentInvalidError,
} from '@bigcommerce/checkout-sdk/payment-integration-api';

function isBlueSnapDirectSepaInstrument(
    data: OrderPaymentRequestBody['paymentData'],
): data is BlueSnapDirectSepaInstrument {
    if (data === undefined) {
        return false;
    }

    return 'iban' in data && 'firstName' in data && 'lastName' in data;
}

export default function assertBlueSnapDirectSepaInstrument(
    data: OrderPaymentRequestBody['paymentData'],
): asserts data is BlueSnapDirectSepaInstrument {
    if (!isBlueSnapDirectSepaInstrument(data)) {
        throw new PaymentArgumentInvalidError();
    }
}
